body{
    background: url("https://images.unsplash.com/photo-1561837581-abd854e0ee22?crop=entropy&cs=tinysrgb&fm=jpg&ixlib=rb-1.2.1&q=80&raw_url=true&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=735");
    background-size: cover;
    color: chartreuse;
    height: 100vh;
    width: auto;
}

.sign-in, .sign-out{

    margin: 1em;;

   
  transition: background-color .3s, box-shadow .3s;
  cursor: pointer;
    
  padding: 12px 16px 12px 42px;
  border: none;
  border-radius: 3px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, .04), 0 1px 1px rgba(0, 0, 0, .25);
  
  color: #757575;
  font-size: 14px;
  font-weight: 500;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  background-repeat: no-repeat;
  background-position: 12px 11px;

    &:hover{

        
        box-shadow: 0 -1px 0 rgba(0, 0, 0, .04), 0 6px 10px rgba(0, 0, 0, .25);
    }
    &-div{
        display: flex;
        justify-content: center;
    }
}









.about-div{
    background-color: rgb(170, 94, 94);
    border: 1px solid black;
    margin: 1em;
    padding: 1em;
    border-radius: 10px;
}

.ex1, .ex2{

    margin-top: 2em;
    margin-bottom: 2em;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);;
    transition: 0.3s;
    border-radius: 5px;
    padding: 2px 16px;
    width: fit-content;
    height: 120px;


    &:hover{
        box-shadow: 0 16px 32px 0 rgba(0,0,0,0.2);;
    }
}
a{
    color: chartreuse;
}

.form-div{

    max-width: 400px;
    border: solid 1px black;
    padding: 1em;
}


.input{
    width: 60%;
    margin: 10px;
 
    padding: 10px;
    outline: none;
    transition: transform .2s;
    justify-content: center;
}

input:hover{
    transform: scale(1.3);

}


.form-div input{
    display: block;
    margin: 5px;
}


.budget-container, .App{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
}

.budget-container{
    padding: 20px;
    width: 500px;
    max-width: 800px;
    height: 80vh;
    border: 1px solid black;
    border-radius: 50px;
}

.clear-budget{
    background-color: red;
    color: white;
    padding: 5px;
    border-radius: 10px;
    margin: 5px;
}

